import React, { useEffect, useState } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import axios from 'axios';
import { Toaster } from '@/components/ui/sonner';
import '@/App.css';

import LoginPage from './pages/LoginPage';
import DashboardLayout from './components/DashboardLayout';
import Dashboard from './pages/Dashboard';
import Products from './pages/Products';
import AIAssistant from './pages/AIAssistant';
import FMEAModule from './pages/FMEAModule';
import Surveys from './pages/Surveys';
import Documents from './pages/Documents';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

axios.defaults.withCredentials = true;

const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
    try {
      // Demo mode: Check localStorage
      const demoUser = localStorage.getItem('eld_demo_user');
      if (demoUser) {
        setUser(JSON.parse(demoUser));
      } else {
        setUser(null);
      }
    } catch (error) {
      console.error('Auth check error:', error);
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-50">
        <div className="text-2xl font-bold text-slate-900">YKLENIYOR...</div>
      </div>
    );
  }

  return children({ user, setUser, checkAuth });
};

const ProtectedRoute = ({ user, children }) => {
  if (!user) {
    return <Navigate to="/login" replace />;
  }
  return children;
};

function App() {
  return (
    <BrowserRouter basename="/platform/eld">
      <AuthProvider>
        {({ user, setUser, checkAuth }) => (
          <>
            <Routes>
              <Route path="/login" element={<LoginPage setUser={setUser} checkAuth={checkAuth} />} />
              <Route
                path="/*"
                element={
                  <ProtectedRoute user={user}>
                    <DashboardLayout user={user} setUser={setUser}>
                      <Routes>
                        <Route path="/" element={<Dashboard />} />
                        <Route path="/products" element={<Products />} />
                        <Route path="/ai-assistant" element={<AIAssistant />} />
                        <Route path="/fmea" element={<FMEAModule />} />
                        <Route path="/surveys" element={<Surveys />} />
                        <Route path="/documents" element={<Documents />} />
                      </Routes>
                    </DashboardLayout>
                  </ProtectedRoute>
                }
              />
            </Routes>
            <Toaster />
          </>
        )}
      </AuthProvider>
    </BrowserRouter>
  );
}

export default App;
