import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Plus, AlertTriangle } from 'lucide-react';
import { toast } from 'sonner';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const FMEAModule = () => {
  const [reports, setReports] = useState([]);
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    product_id: '',
    failure_mode: '',
    effects: '',
    severity: 5,
    occurrence: 5,
    detection: 5,
    recommended_action: '',
  });

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [reportsRes, productsRes] = await Promise.all([
        axios.get(`${API}/fmea`),
        axios.get(`${API}/products`),
      ]);
      setReports(reportsRes.data);
      setProducts(productsRes.data);
    } catch (error) {
      toast.error('Veriler yklenemedi');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post(`${API}/fmea`, formData);
      toast.success('FMEA raporu baaryla eklendi');
      setDialogOpen(false);
      setFormData({
        product_id: '',
        failure_mode: '',
        effects: '',
        severity: 5,
        occurrence: 5,
        detection: 5,
        recommended_action: '',
      });
      fetchData();
    } catch (error) {
      toast.error('Rapor eklenemedi');
    }
  };

  const getRiskLevel = (rpn) => {
    if (rpn >= 200) return { label: 'Kritik', color: 'bg-red-500' };
    if (rpn >= 100) return { label: 'Yksek', color: 'bg-orange-500' };
    if (rpn >= 50) return { label: 'Orta', color: 'bg-amber-500' };
    return { label: 'Dk', color: 'bg-emerald-500' };
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
            FMEA Analizi
          </h1>
          <p className="text-lg text-slate-600">Hata Modu Etki Analizi</p>
        </div>
        <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
          <DialogTrigger asChild>
            <Button
              data-testid="add-fmea-btn"
              className="flex items-center gap-2 bg-slate-900 text-white font-bold uppercase h-12 px-8 rounded-sm hover:bg-slate-800"
            >
              <Plus className="h-5 w-5" />
              Yeni Rapor
            </Button>
          </DialogTrigger>
          <DialogContent className="rounded-none border-4 border-slate-900 max-w-2xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle className="text-2xl font-bold uppercase">Yeni FMEA Raporu</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Label htmlFor="product" className="font-bold">rn</Label>
                <Select
                  value={formData.product_id}
                  onValueChange={(value) => setFormData({ ...formData, product_id: value })}
                >
                  <SelectTrigger data-testid="fmea-product-select" className="rounded-none border-2">
                    <SelectValue placeholder="rn sein" />
                  </SelectTrigger>
                  <SelectContent>
                    {products.map((product) => (
                      <SelectItem key={product.id} value={product.id}>
                        {product.name} ({product.model_number})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Label htmlFor="failure_mode" className="font-bold">Hata Modu</Label>
                <Input
                  id="failure_mode"
                  data-testid="fmea-failure-input"
                  value={formData.failure_mode}
                  onChange={(e) => setFormData({ ...formData, failure_mode: e.target.value })}
                  className="rounded-none border-2"
                  required
                />
              </div>
              <div>
                <Label htmlFor="effects" className="font-bold">Etkileri</Label>
                <Textarea
                  id="effects"
                  data-testid="fmea-effects-input"
                  value={formData.effects}
                  onChange={(e) => setFormData({ ...formData, effects: e.target.value })}
                  className="rounded-none border-2"
                  rows={2}
                  required
                />
              </div>
              <div className="grid grid-cols-3 gap-4">
                <div>
                  <Label htmlFor="severity" className="font-bold">Ciddiyet (1-10)</Label>
                  <Input
                    id="severity"
                    data-testid="fmea-severity-input"
                    type="number"
                    min="1"
                    max="10"
                    value={formData.severity}
                    onChange={(e) => setFormData({ ...formData, severity: parseInt(e.target.value) })}
                    className="rounded-none border-2"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="occurrence" className="font-bold">Oluma (1-10)</Label>
                  <Input
                    id="occurrence"
                    data-testid="fmea-occurrence-input"
                    type="number"
                    min="1"
                    max="10"
                    value={formData.occurrence}
                    onChange={(e) => setFormData({ ...formData, occurrence: parseInt(e.target.value) })}
                    className="rounded-none border-2"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="detection" className="font-bold">Tespit (1-10)</Label>
                  <Input
                    id="detection"
                    data-testid="fmea-detection-input"
                    type="number"
                    min="1"
                    max="10"
                    value={formData.detection}
                    onChange={(e) => setFormData({ ...formData, detection: parseInt(e.target.value) })}
                    className="rounded-none border-2"
                    required
                  />
                </div>
              </div>
              <div>
                <Label htmlFor="action" className="font-bold">nerilen Aksiyon</Label>
                <Textarea
                  id="action"
                  data-testid="fmea-action-input"
                  value={formData.recommended_action}
                  onChange={(e) => setFormData({ ...formData, recommended_action: e.target.value })}
                  className="rounded-none border-2"
                  rows={2}
                />
              </div>
              <Button
                type="submit"
                data-testid="submit-fmea-btn"
                className="w-full bg-slate-900 text-white font-bold uppercase h-12 rounded-sm"
              >
                Rapor Ekle
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Reports Table */}
      {loading ? (
        <div className="text-center py-12">
          <p className="text-xl font-bold text-slate-600">Ykleniyor...</p>
        </div>
      ) : reports.length === 0 ? (
        <div className="bg-white rounded-sm border-2 border-slate-900 p-12 text-center">
          <AlertTriangle className="h-16 w-16 text-slate-400 mx-auto mb-4" />
          <h3 className="text-xl font-bold text-slate-900 mb-2">Henz rapor yok</h3>
          <p className="text-slate-600 mb-6">Balamak iin ilk FMEA raporunuzu ekleyin</p>
        </div>
      ) : (
        <div className="bg-white rounded-sm border-2 border-slate-900 overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-slate-900 text-white">
                <tr>
                  <th className="px-4 py-3 text-left font-bold uppercase text-sm">Hata Modu</th>
                  <th className="px-4 py-3 text-left font-bold uppercase text-sm">Etki</th>
                  <th className="px-4 py-3 text-center font-bold uppercase text-sm">S</th>
                  <th className="px-4 py-3 text-center font-bold uppercase text-sm">O</th>
                  <th className="px-4 py-3 text-center font-bold uppercase text-sm">D</th>
                  <th className="px-4 py-3 text-center font-bold uppercase text-sm">RPN</th>
                  <th className="px-4 py-3 text-center font-bold uppercase text-sm">Risk</th>
                </tr>
              </thead>
              <tbody>
                {reports.map((report, index) => {
                  const risk = getRiskLevel(report.rpn);
                  return (
                    <tr
                      key={report.id}
                      data-testid={`fmea-report-row-${index}`}
                      className="border-b-2 border-slate-200 hover:bg-slate-50"
                    >
                      <td className="px-4 py-3 font-bold">{report.failure_mode}</td>
                      <td className="px-4 py-3 text-sm">{report.effects}</td>
                      <td className="px-4 py-3 text-center font-mono font-bold">{report.severity}</td>
                      <td className="px-4 py-3 text-center font-mono font-bold">{report.occurrence}</td>
                      <td className="px-4 py-3 text-center font-mono font-bold">{report.detection}</td>
                      <td className="px-4 py-3 text-center font-mono font-black text-lg">{report.rpn}</td>
                      <td className="px-4 py-3 text-center">
                        <span className={`${risk.color} text-white px-3 py-1 rounded-full text-xs font-bold uppercase`}>
                          {risk.label}
                        </span>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
};

export default FMEAModule;
