import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Plus, ClipboardList, Trash2 } from 'lucide-react';
import { toast } from 'sonner';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const Surveys = () => {
  const [surveys, setSurveys] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    title: '',
    questions: [{ text: '', type: 'text' }],
  });

  useEffect(() => {
    fetchSurveys();
  }, []);

  const fetchSurveys = async () => {
    try {
      const response = await axios.get(`${API}/surveys`);
      setSurveys(response.data);
    } catch (error) {
      toast.error('Anketler yklenemedi');
    } finally {
      setLoading(false);
    }
  };

  const addQuestion = () => {
    setFormData({
      ...formData,
      questions: [...formData.questions, { text: '', type: 'text' }],
    });
  };

  const removeQuestion = (index) => {
    const newQuestions = formData.questions.filter((_, i) => i !== index);
    setFormData({ ...formData, questions: newQuestions });
  };

  const updateQuestion = (index, value) => {
    const newQuestions = [...formData.questions];
    newQuestions[index].text = value;
    setFormData({ ...formData, questions: newQuestions });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (formData.questions.some((q) => !q.text.trim())) {
      toast.error('Tm sorular doldurun');
      return;
    }
    try {
      await axios.post(`${API}/surveys`, formData);
      toast.success('Anket baaryla oluturuldu');
      setDialogOpen(false);
      setFormData({ title: '', questions: [{ text: '', type: 'text' }] });
      fetchSurveys();
    } catch (error) {
      toast.error('Anket oluturulamad');
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
            Memnuniyet Anketleri
          </h1>
          <p className="text-lg text-slate-600">Mteri geri bildirim ynetimi</p>
        </div>
        <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
          <DialogTrigger asChild>
            <Button
              data-testid="add-survey-btn"
              className="flex items-center gap-2 bg-slate-900 text-white font-bold uppercase h-12 px-8 rounded-sm hover:bg-slate-800"
            >
              <Plus className="h-5 w-5" />
              Yeni Anket
            </Button>
          </DialogTrigger>
          <DialogContent className="rounded-none border-4 border-slate-900 max-w-2xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle className="text-2xl font-bold uppercase">Yeni Anket Olutur</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Label htmlFor="title" className="font-bold">Anket Bal</Label>
                <Input
                  id="title"
                  data-testid="survey-title-input"
                  value={formData.title}
                  onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                  className="rounded-none border-2"
                  required
                />
              </div>
              
              <div className="space-y-3">
                <Label className="font-bold">Sorular</Label>
                {formData.questions.map((question, index) => (
                  <div key={index} className="flex gap-2">
                    <Input
                      data-testid={`survey-question-${index}`}
                      value={question.text}
                      onChange={(e) => updateQuestion(index, e.target.value)}
                      placeholder={`Soru ${index + 1}`}
                      className="rounded-none border-2 flex-1"
                    />
                    {formData.questions.length > 1 && (
                      <Button
                        type="button"
                        data-testid={`remove-question-${index}`}
                        onClick={() => removeQuestion(index)}
                        className="bg-red-500 text-white px-3 rounded-sm hover:bg-red-600"
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    )}
                  </div>
                ))}
                <Button
                  type="button"
                  data-testid="add-question-btn"
                  onClick={addQuestion}
                  className="w-full bg-slate-200 text-slate-900 font-bold uppercase h-10 rounded-sm hover:bg-slate-300"
                >
                  + Soru Ekle
                </Button>
              </div>

              <Button
                type="submit"
                data-testid="submit-survey-btn"
                className="w-full bg-slate-900 text-white font-bold uppercase h-12 rounded-sm"
              >
                Anket Olutur
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Surveys Grid */}
      {loading ? (
        <div className="text-center py-12">
          <p className="text-xl font-bold text-slate-600">Ykleniyor...</p>
        </div>
      ) : surveys.length === 0 ? (
        <div className="bg-white rounded-sm border-2 border-slate-900 p-12 text-center">
          <ClipboardList className="h-16 w-16 text-slate-400 mx-auto mb-4" />
          <h3 className="text-xl font-bold text-slate-900 mb-2">Henz anket yok</h3>
          <p className="text-slate-600 mb-6">Balamak iin ilk anketinizi oluturun</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {surveys.map((survey) => (
            <div
              key={survey.id}
              data-testid={`survey-card-${survey.id}`}
              className="bg-white rounded-sm border-2 border-slate-900 hard-shadow p-6 transition-all duration-200"
            >
              <div className="flex items-start justify-between mb-4">
                <div className="flex-1">
                  <h3 className="text-xl font-bold text-slate-900 mb-2">{survey.title}</h3>
                  <p className="text-sm text-slate-600">{survey.questions.length} soru</p>
                </div>
                <Badge
                  className={`rounded-full px-3 py-1 text-xs font-bold uppercase ${
                    survey.active ? 'bg-emerald-500' : 'bg-slate-400'
                  }`}
                >
                  {survey.active ? 'Aktif' : 'Pasif'}
                </Badge>
              </div>
              <div className="space-y-2">
                {survey.questions.slice(0, 3).map((question, index) => (
                  <div key={index} className="text-sm text-slate-600 flex items-start gap-2">
                    <span className="font-mono font-bold text-slate-400">{index + 1}.</span>
                    <span>{question.text}</span>
                  </div>
                ))}
                {survey.questions.length > 3 && (
                  <p className="text-xs text-slate-500">+{survey.questions.length - 3} soru daha...</p>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Surveys;
