import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Plus, Package } from 'lucide-react';
import { toast } from 'sonner';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const Products = () => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    model_number: '',
    description: '',
  });

  useEffect(() => {
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    try {
      const response = await axios.get(`${API}/products`);
      setProducts(response.data);
    } catch (error) {
      toast.error('rnler yklenemedi');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post(`${API}/products`, formData);
      toast.success('rn baaryla eklendi');
      setDialogOpen(false);
      setFormData({ name: '', model_number: '', description: '' });
      fetchProducts();
    } catch (error) {
      toast.error('rn eklenemedi');
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
            rnler
          </h1>
          <p className="text-lg text-slate-600">Montaj talimatlar ve bakm klavuzlar</p>
        </div>
        <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
          <DialogTrigger asChild>
            <Button
              data-testid="add-product-btn"
              className="flex items-center gap-2 bg-slate-900 text-white font-bold uppercase h-12 px-8 rounded-sm hover:bg-slate-800"
            >
              <Plus className="h-5 w-5" />
              Yeni rn
            </Button>
          </DialogTrigger>
          <DialogContent className="rounded-none border-4 border-slate-900">
            <DialogHeader>
              <DialogTitle className="text-2xl font-bold uppercase">Yeni rn Ekle</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Label htmlFor="name" className="font-bold">rn Ad</Label>
                <Input
                  id="name"
                  data-testid="product-name-input"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="rounded-none border-2"
                  required
                />
              </div>
              <div>
                <Label htmlFor="model" className="font-bold">Model Numaras</Label>
                <Input
                  id="model"
                  data-testid="product-model-input"
                  value={formData.model_number}
                  onChange={(e) => setFormData({ ...formData, model_number: e.target.value })}
                  className="rounded-none border-2"
                  required
                />
              </div>
              <div>
                <Label htmlFor="description" className="font-bold">Aklama</Label>
                <Textarea
                  id="description"
                  data-testid="product-description-input"
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  className="rounded-none border-2"
                  rows={3}
                />
              </div>
              <Button
                type="submit"
                data-testid="submit-product-btn"
                className="w-full bg-slate-900 text-white font-bold uppercase h-12 rounded-sm"
              >
                Ekle
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Products Grid */}
      {loading ? (
        <div className="text-center py-12">
          <p className="text-xl font-bold text-slate-600">Ykleniyor...</p>
        </div>
      ) : products.length === 0 ? (
        <div className="bg-white rounded-sm border-2 border-slate-900 p-12 text-center">
          <Package className="h-16 w-16 text-slate-400 mx-auto mb-4" />
          <h3 className="text-xl font-bold text-slate-900 mb-2">Henz rn yok</h3>
          <p className="text-slate-600 mb-6">Balamak iin ilk rnnz ekleyin</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {products.map((product) => (
            <div
              key={product.id}
              data-testid={`product-card-${product.id}`}
              className="bg-white rounded-sm border-2 border-slate-900 hard-shadow p-6 transition-all duration-200"
            >
              <div className="flex items-start justify-between mb-4">
                <div className="bg-slate-900 w-12 h-12 rounded-sm flex items-center justify-center">
                  <Package className="h-6 w-6 text-white" />
                </div>
              </div>
              <h3 className="text-xl font-bold text-slate-900 mb-2">{product.name}</h3>
              <p className="text-sm font-mono text-slate-500 mb-2">Model: {product.model_number}</p>
              {product.description && (
                <p className="text-sm text-slate-600 line-clamp-2">{product.description}</p>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Products;
