import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { LogIn, Wrench } from 'lucide-react';
import axios from 'axios';
import { toast } from 'sonner';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const LoginPage = ({ setUser, checkAuth }) => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const handleLogin = async () => {
    setLoading(true);
    try {
      // Demo login - No backend required
      const demoUser = {
        id: 'demo-user-001',
        name: 'Demo Kullanc',
        email: 'demo@eld.system',
        role: 'admin',
        avatar: null
      };

      // Simulate network delay for realism
      await new Promise(resolve => setTimeout(resolve, 800));

      localStorage.setItem('eld_demo_user', JSON.stringify(demoUser));
      setUser(demoUser);

      // Force navigation to dashboard to bypass any router state issues
      window.location.href = '/platform/eld/';
      toast.success('Giri baarl');
    } catch (error) {
      console.error('Login error:', error);
      toast.error('Giri yaplamad');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-slate-50 flex items-center justify-center p-4">
      <div className="max-w-md w-full">
        {/* Hero Card */}
        <div className="bg-white rounded-sm border-2 border-slate-900 hard-shadow mb-8 overflow-hidden">
          <div className="bg-slate-900 p-8 text-center">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-orange-500 rounded-sm mb-4">
              <Wrench className="h-8 w-8 text-white" strokeWidth={2} />
            </div>
            <h1 className="text-3xl font-black text-white uppercase tracking-tighter mb-2">
              ELD System
            </h1>
            <p className="text-slate-400 font-medium">
              Entegre Lojistik Destek Program
            </p>
          </div>

          <div className="p-8">
            <div className="space-y-4 mb-6">
              <div className="flex items-start gap-3">
                <div className="w-2 h-2 bg-orange-500 rounded-full mt-2" />
                <div>
                  <h3 className="font-bold text-slate-900">rn Montaj Talimatlar</h3>
                  <p className="text-sm text-slate-600">Detayl montaj dokmanlar ve adm adm rehberler</p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <div className="w-2 h-2 bg-orange-500 rounded-full mt-2" />
                <div>
                  <h3 className="font-bold text-slate-900">AI Destekli Bakm Asistan</h3>
                  <p className="text-sm text-slate-600">Akll hata tehis ve onarm rehberi</p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <div className="w-2 h-2 bg-orange-500 rounded-full mt-2" />
                <div>
                  <h3 className="font-bold text-slate-900">FMEA Analizi</h3>
                  <p className="text-sm text-slate-600">Hata Modu Etki Analizi ve raporlama</p>
                </div>
              </div>
            </div>

            <button
              data-testid="login-btn"
              onClick={handleLogin}
              disabled={loading}
              className="w-full flex items-center justify-center gap-3 bg-slate-900 text-white font-bold uppercase text-sm px-8 py-4 rounded-sm hover:bg-slate-800 transition-colors duration-200 disabled:opacity-50"
            >
              <LogIn className="h-5 w-5" />
              {loading ? 'Giri Yaplyor...' : 'Giri Yap (Demo)'}
            </button>

            <p className="text-xs text-slate-500 text-center mt-4">
              Demo srm - Otomatik giri yaplacak
            </p>
          </div>
        </div>

        <div className="text-center text-sm text-slate-600">
          <p className="font-mono"> 2025 ELD System - Tm haklar sakldr</p>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
