import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { Upload, FileText, Download } from 'lucide-react';
import { toast } from 'sonner';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const Documents = () => {
  const [documents, setDocuments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);
  const [docType, setDocType] = useState('maintenance');
  const fileInputRef = useRef(null);

  useEffect(() => {
    fetchDocuments();
  }, []);

  const fetchDocuments = async () => {
    try {
      const response = await axios.get(`${API}/documents`);
      setDocuments(response.data);
    } catch (error) {
      toast.error('Dokmanlar yklenemedi');
    } finally {
      setLoading(false);
    }
  };

  const handleFileUpload = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;

    setUploading(true);
    const formData = new FormData();
    formData.append('file', file);
    formData.append('doc_type', docType);

    try {
      await axios.post(`${API}/documents/upload`, formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      toast.success('Dokman baaryla yklendi');
      fetchDocuments();
    } catch (error) {
      toast.error('Dokman yklenemedi');
    } finally {
      setUploading(false);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  const handleDownload = async (docId, filename) => {
    try {
      const response = await axios.get(`${API}/documents/${docId}/download`, {
        responseType: 'blob',
      });
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', filename);
      document.body.appendChild(link);
      link.click();
      link.remove();
      toast.success('Dokman indirildi');
    } catch (error) {
      toast.error('Dokman indirilemedi');
    }
  };

  const getTypeLabel = (type) => {
    const types = {
      assembly: 'Montaj',
      maintenance: 'Bakm',
      quality: 'Kalite',
    };
    return types[type] || type;
  };

  const getTypeColor = (type) => {
    const colors = {
      assembly: 'bg-blue-500',
      maintenance: 'bg-green-500',
      quality: 'bg-purple-500',
    };
    return colors[type] || 'bg-slate-500';
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
          Dokman Merkezi
        </h1>
        <p className="text-lg text-slate-600">Tm teknik dokmanlar ve klavuzlar</p>
      </div>

      {/* Upload Section */}
      <div className="bg-white rounded-sm border-2 border-slate-900 hard-shadow p-6">
        <h2 className="text-xl font-bold text-slate-900 mb-4 uppercase">Yeni Dokman Ykle</h2>
        <div className="flex gap-4 items-end">
          <div className="flex-1">
            <label className="block text-sm font-bold text-slate-700 mb-2">Dokman Tipi</label>
            <Select value={docType} onValueChange={setDocType}>
              <SelectTrigger data-testid="doc-type-select" className="rounded-none border-2">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="assembly">Montaj Talimat</SelectItem>
                <SelectItem value="maintenance">Bakm Klavuzu</SelectItem>
                <SelectItem value="quality">Kalite Dokman</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div className="flex-1">
            <input
              ref={fileInputRef}
              data-testid="file-upload-input"
              type="file"
              onChange={handleFileUpload}
              accept=".pdf,.doc,.docx,.txt"
              className="hidden"
              id="file-upload"
            />
            <Button
              data-testid="upload-doc-btn"
              onClick={() => fileInputRef.current?.click()}
              disabled={uploading}
              className="w-full bg-slate-900 text-white font-bold uppercase h-12 px-8 rounded-sm hover:bg-slate-800 disabled:opacity-50"
            >
              {uploading ? (
                'Ykleniyor...'
              ) : (
                <>
                  <Upload className="h-5 w-5 mr-2" />
                  Dosya Se
                </>
              )}
            </Button>
          </div>
        </div>
        <p className="text-xs text-slate-500 mt-2">
          Desteklenen formatlar: PDF, DOC, DOCX, TXT
        </p>
      </div>

      {/* Documents List */}
      {loading ? (
        <div className="text-center py-12">
          <p className="text-xl font-bold text-slate-600">Ykleniyor...</p>
        </div>
      ) : documents.length === 0 ? (
        <div className="bg-white rounded-sm border-2 border-slate-900 p-12 text-center">
          <FileText className="h-16 w-16 text-slate-400 mx-auto mb-4" />
          <h3 className="text-xl font-bold text-slate-900 mb-2">Henz dokman yok</h3>
          <p className="text-slate-600 mb-6">Balamak iin ilk dokmannz ykleyin</p>
        </div>
      ) : (
        <div className="bg-white rounded-sm border-2 border-slate-900 overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-slate-900 text-white">
                <tr>
                  <th className="px-4 py-3 text-left font-bold uppercase text-sm">Dokman</th>
                  <th className="px-4 py-3 text-left font-bold uppercase text-sm">Tip</th>
                  <th className="px-4 py-3 text-right font-bold uppercase text-sm">Aksiyonlar</th>
                </tr>
              </thead>
              <tbody>
                {documents.map((doc, index) => (
                  <tr
                    key={doc.id}
                    data-testid={`doc-row-${index}`}
                    className="border-b-2 border-slate-200 hover:bg-slate-50"
                  >
                    <td className="px-4 py-3">
                      <div className="flex items-center gap-3">
                        <div className="bg-slate-900 w-10 h-10 rounded-sm flex items-center justify-center">
                          <FileText className="h-5 w-5 text-white" />
                        </div>
                        <span className="font-bold">{doc.name}</span>
                      </div>
                    </td>
                    <td className="px-4 py-3">
                      <span className={`${getTypeColor(doc.type)} text-white px-3 py-1 rounded-full text-xs font-bold uppercase`}>
                        {getTypeLabel(doc.type)}
                      </span>
                    </td>
                    <td className="px-4 py-3 text-right">
                      <Button
                        data-testid={`download-doc-${index}`}
                        onClick={() => handleDownload(doc.id, doc.name)}
                        className="bg-slate-200 text-slate-900 font-bold uppercase px-4 py-2 rounded-sm hover:bg-slate-300"
                      >
                        <Download className="h-4 w-4 mr-2" />
                        ndir
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
};

export default Documents;
