import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Send, Bot, User } from 'lucide-react';
import { toast } from 'sonner';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const AIAssistant = () => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [sessionId, setSessionId] = useState(null);
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = async (e) => {
    e.preventDefault();
    if (!input.trim() || loading) return;

    const userMessage = {
      role: 'user',
      content: input,
      id: Date.now(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setLoading(true);

    try {
      const response = await axios.post(`${API}/ai/chat`, {
        message: input,
        session_id: sessionId,
      });

      setSessionId(response.data.session_id);

      const aiMessage = {
        role: 'assistant',
        content: response.data.response,
        id: Date.now() + 1,
      };

      setMessages((prev) => [...prev, aiMessage]);
    } catch (error) {
      toast.error('Mesaj gnderilemedi');
      console.error('Chat error:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="h-[calc(100vh-12rem)] flex flex-col">
      {/* Header */}
      <div className="mb-6">
        <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
          AI Bakm Asistan
        </h1>
        <p className="text-lg text-slate-600">
          Akll hata tehis ve onarm rehberi
        </p>
      </div>

      {/* Chat Container - Dark Terminal Theme */}
      <div className="flex-1 flex flex-col bg-slate-950 rounded-sm border-2 border-slate-900 overflow-hidden terminal-glow">
        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-6 space-y-4">
          {messages.length === 0 ? (
            <div className="h-full flex items-center justify-center">
              <div className="text-center max-w-md">
                <div className="bg-orange-500 w-16 h-16 rounded-sm flex items-center justify-center mx-auto mb-4">
                  <Bot className="h-8 w-8 text-white" />
                </div>
                <h3 className="text-xl font-bold text-white mb-2">Terminal Modu Aktif</h3>
                <p className="text-slate-400 font-mono text-sm">
                  Sistemle etkileime gemek iin bir mesaj yazn...
                </p>
              </div>
            </div>
          ) : (
            messages.map((message) => (
              <div
                key={message.id}
                data-testid={`chat-message-${message.role}`}
                className={`flex gap-3 ${
                  message.role === 'user' ? 'justify-end' : 'justify-start'
                }`}
              >
                {message.role === 'assistant' && (
                  <div className="bg-orange-500 w-8 h-8 rounded-sm flex items-center justify-center flex-shrink-0">
                    <Bot className="h-5 w-5 text-white" />
                  </div>
                )}
                <div
                  className={`max-w-2xl px-4 py-3 rounded-sm ${
                    message.role === 'user'
                      ? 'bg-slate-800 text-white border-2 border-slate-700'
                      : 'bg-slate-900 text-emerald-400 border-2 border-emerald-500/30'
                  }`}
                >
                  <p className="font-mono text-sm whitespace-pre-wrap leading-relaxed">
                    {message.content}
                  </p>
                </div>
                {message.role === 'user' && (
                  <div className="bg-slate-700 w-8 h-8 rounded-sm flex items-center justify-center flex-shrink-0">
                    <User className="h-5 w-5 text-white" />
                  </div>
                )}
              </div>
            ))
          )}
          {loading && (
            <div className="flex gap-3">
              <div className="bg-orange-500 w-8 h-8 rounded-sm flex items-center justify-center">
                <Bot className="h-5 w-5 text-white" />
              </div>
              <div className="bg-slate-900 px-4 py-3 rounded-sm border-2 border-emerald-500/30">
                <p className="font-mono text-sm text-emerald-400">Analiz ediliyor...</p>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Input */}
        <div className="border-t-2 border-slate-800 p-4 bg-slate-900">
          <form onSubmit={handleSend} className="flex gap-3">
            <Textarea
              data-testid="ai-chat-input"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="> Komut girin..."
              className="flex-1 bg-slate-950 text-white border-2 border-slate-700 rounded-sm font-mono text-sm resize-none focus:border-emerald-500 placeholder:text-slate-600"
              rows={2}
              onKeyDown={(e) => {
                if (e.key === 'Enter' && !e.shiftKey) {
                  e.preventDefault();
                  handleSend(e);
                }
              }}
            />
            <Button
              type="submit"
              data-testid="send-chat-btn"
              disabled={loading || !input.trim()}
              className="bg-orange-500 text-white font-bold uppercase h-auto px-6 rounded-sm hover:bg-orange-600 disabled:opacity-50"
            >
              <Send className="h-5 w-5" />
            </Button>
          </form>
          <p className="text-xs text-slate-600 font-mono mt-2">
            ENTER: Gnder | SHIFT+ENTER: Yeni satr
          </p>
        </div>
      </div>
    </div>
  );
};

export default AIAssistant;
