import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Package, FileText, AlertTriangle, ClipboardList, TrendingUp } from 'lucide-react';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const Dashboard = () => {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const response = await axios.get(`${API}/dashboard/stats`);
      setStats(response.data);
    } catch (error) {
      console.error('Stats fetch error:', error);
    } finally {
      setLoading(false);
    }
  };

  const statCards = [
    {
      title: 'rnler',
      value: stats?.products || 0,
      icon: Package,
      color: 'bg-blue-500',
    },
    {
      title: 'Dokmanlar',
      value: stats?.documents || 0,
      icon: FileText,
      color: 'bg-green-500',
    },
    {
      title: 'FMEA Raporlar',
      value: stats?.fmea_reports || 0,
      icon: AlertTriangle,
      color: 'bg-amber-500',
    },
    {
      title: 'Anketler',
      value: stats?.surveys || 0,
      icon: ClipboardList,
      color: 'bg-purple-500',
    },
    {
      title: 'Yksek Risk eleri',
      value: stats?.high_risk_items || 0,
      icon: TrendingUp,
      color: 'bg-red-500',
    },
  ];

  if (loading) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-2xl font-bold text-slate-900">Ykleniyor...</div>
      </div>
    );
  }

  return (
    <div className="space-y-8">
      {/* Header */}
      <div>
        <h1 className="text-4xl font-black uppercase tracking-tighter text-slate-900 mb-2">
          Kontrol Paneli
        </h1>
        <p className="text-lg text-slate-600">
          Sistem zetine ho geldiniz
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {statCards.map((stat, index) => {
          const Icon = stat.icon;
          return (
            <div
              key={index}
              data-testid={`stat-card-${stat.title.toLowerCase().replace(/\s+/g, '-')}`}
              className="bg-white rounded-sm border-2 border-slate-900 hard-shadow p-6 transition-all duration-200"
            >
              <div className="flex items-start justify-between mb-4">
                <div className={`${stat.color} w-12 h-12 rounded-sm flex items-center justify-center`}>
                  <Icon className="h-6 w-6 text-white" strokeWidth={2} />
                </div>
              </div>
              <div>
                <div className="text-3xl font-black text-slate-900 mb-1">{stat.value}</div>
                <div className="text-sm font-bold uppercase tracking-wider text-slate-500">
                  {stat.title}
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-sm border-2 border-slate-900 hard-shadow p-8">
        <h2 className="text-2xl font-bold text-slate-900 mb-6 uppercase tracking-tight">
          Hzl Eriim
        </h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <a
            href="/products"
            data-testid="quick-action-products"
            className="flex items-center gap-3 p-4 border-2 border-slate-900 rounded-sm hover:bg-slate-50 transition-colors duration-200"
          >
            <Package className="h-6 w-6" />
            <span className="font-bold">rn Ekle</span>
          </a>
          <a
            href="/ai-assistant"
            data-testid="quick-action-ai"
            className="flex items-center gap-3 p-4 border-2 border-slate-900 rounded-sm hover:bg-slate-50 transition-colors duration-200"
          >
            <Package className="h-6 w-6" />
            <span className="font-bold">AI Asistan</span>
          </a>
          <a
            href="/documents"
            data-testid="quick-action-docs"
            className="flex items-center gap-3 p-4 border-2 border-slate-900 rounded-sm hover:bg-slate-50 transition-colors duration-200"
          >
            <FileText className="h-6 w-6" />
            <span className="font-bold">Dokman Ykle</span>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
